*, :before, :after {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, h5, h6, p, figure, blockquote, dl, dd {
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  font: inherit;
  color: inherit;
  background: none;
  border: none;
  padding: 0;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: sans-serif;
  line-height: 1.5;
}

.athletes {
  flex-wrap: wrap;
  gap: 30px;
  margin: 30px 35px;
  display: flex;
}

.athletes:after {
  content: "";
  flex: auto;
}

.athlete {
  width: 200px;
  position: relative;
}

.athlete__photo {
  border: 3px solid #d16e11;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  overflow: hidden;
}

.athlete__rank {
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.athlete__details {
  font-family: Arial, Helvetica, sans-serif;
}

.athlete__name {
  color: #2f4f4f;
  text-align: center;
  margin-top: 8px;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
  display: block;
}

.athlete__extra {
  color: #000;
  text-align: center;
  margin-top: 8px;
  font-size: 21.6px;
  font-weight: 400;
  line-height: 100%;
  display: block;
}
